<template>
  <main
    class="background"
    :class="{ orange: !isConnected, blanc: isConnected, }"
    @is-connected="isConnected"
  >
    <div
      class="menu-logo"
    >
      <img
        id="logo"
        src="@/assets/images/PopLab-baseline-black.svg"
        :class="{ logolight: isConnected, logofull: !isConnected, }"
        alt="Logo PopLab"
      >
      <button
        v-if="isConnected"
        :class="{ buttonon: options, buttonoff: !options, }"
        @click="options = !options"
      >
        <IconSettings />
      </button>
      <img
        id="close"
        src="@/assets/images/close.svg"
        :class="{ mrleft10: isConnected, mrleft100: !isConnected, }"
        alt="Close Icon"
        @click="closePopup()"
      >
    </div>
    <Login
      v-if="!isConnected"
      @is-connected="updateStatus($event)"
      @user-firstname="updateUser($event)"
      @firstName="updateFirstName($event)"
      @useClipper="updateClipper($event)"
    />
    <Param
      v-else-if="isConnected && options"
      @deconnexion="deconnecter()"
    />
    <template
      v-if="isConnected && !options"
    >
      <MenuCapture
        v-if="!paramCapture"
        :prop-user="userName"
        @parSignet="choixCapture('signet')"
      />
    </template>
  </main>
</template>
<script type="text/javascript" src="browser-polyfill.min.js"></script>
<script>
import Param from "@/components/Param.vue";
import Login from "@/components/Login.vue";
import MenuCapture from "@/components/MenuCapture.vue";
import IconSettings from "@/components/IconSettings.vue";
import { updateMenus, seDeconnecter } from "../content_scripts/poplink";

export default {
  name: "App",
  components: {
    Login,
    Param,
    MenuCapture,
    IconSettings,
  },
  props: {
    userName: {
      type: String,
      default: ""
    },
    isConnected: {
      type: Boolean,
      default: false
    },
  },
  data: () => ({
    options: false,
    paramCapture: false,
  }),
  beforeMount() {
    browser.contextMenus.onClicked.addListener(async (info, tab) => {
    if (info.menuItemId === "se-deconnecter" || !this.isConnected) {
      seDeconnecter();
      notifierOngletActif({ type: "stopCapture" });
    } else if (info.menuItemId === "voir-frigo") {
      browser.tabs.create({ url: urlFrigo });
    } else if (info.menuItemId === "envoyer-lien") {
      notifierOngletActif({ type: "capturerLien" });
    } else if (info.menuItemId === "envoyer-image") {
      notifierOngletActif({ type: "capturerImage" });
    } else if (info.menuItemId === "envoyer-video") {
      notifierOngletActif({ type: "capturerVideo" });
    }
  });
    const loggedOn = localStorage.first_name;
    const canClipper = localStorage.use_clipper;
    if (loggedOn && !canClipper) {
      this.isConnected = true;
      this.userName = loggedOn;
      updateMenus(true);
    }
  },
  mounted() {
    const formElement = document.querySelector("form");
    if (formElement) {
      formElement.addEventListener("submit", async (ev) => {
        ev.preventDefault();
        try { browser.runtime.sendMessage({
        type: "connect",
        });
        window.close();
        } catch (err) {}
      });
    };
  },
  methods: {
    deconnecter() {
      seDeconnecter();
      this.isConnected = false;
    },
    updateStatus(statut) {
      this.isConnected = statut;
    },
    updateUser(user) {
      this.userName = user;
    },
    closePopup() {
      window.close();
    },
    choixCapture(type) {
      this.paramCapture = true;
      // browser.tabs.query({ active: true, currentWindow: true }).then(tabs => {
      //   browser.tabs.sendMessage(tabs[0].id, {
      //     msg: { type: "basique"}
      //   });
      // });
    },
  },
};
</script>
<style lang="scss">
@import '@/assets/stylesheets/styles.scss';
</style>
