/*
 * poplink.js - fonctions g√©n√©rales pour communiquer avec PopLab
 *
 * Les fonctions ci-dessous servent avant tout √† g√©rer l'authentification,
 * qui est requise pour 99% des appels.
 * Elles sont toutes asynchrones, donc n'oubliez pas `await`.
 */
const urlAuth = "https://id.integration-its.poplab.education/auth/realms/lde-poplab/protocol/openid-connect";
const browser = require("webextension-polyfill");

async function updateMenus(connecte) {
  await browser.contextMenus.update("se-deconnecter", { enabled: connecte });
  await browser.contextMenus.update("envoyer-lien", { enabled: connecte });
  await browser.contextMenus.update("envoyer-image", { enabled: connecte });
  await browser.contextMenus.update("envoyer-video", { enabled: connecte });
}
/**
     * Appel du login sur le serveur keycloak via la fonction
     * "launchWebAuthFlow" d√©di√©e aux extensions de navigateurs
     * @param {objet JSON} url
     * @returns {objet url} - Une url qui sera "slic√©e"" pour en extraire les donn√©es via un split
     * @see https://developer.mozilla.org/fr/docs/Mozilla/Add-ons/WebExtensions/API/identity/launchWebAuthFlow
     */
async function login() {
  const redirectURL = browser.identity.getRedirectURL();
  const clientID = "poplab-front";
  const scopes = "openid";

  let loginURL = `${urlAuth}/auth`;
  loginURL += `?client_id=${clientID}`;
  loginURL += "&response_type=code";
  loginURL += "&state = default";
  loginURL += `&redirect_uri=${encodeURIComponent(redirectURL)}`;
  loginURL += `&scope=${encodeURIComponent(scopes)}`;
  let url = {};
  url = await browser.identity.launchWebAuthFlow({
    interactive: true,
    url: loginURL,
  });
  let hash;
  const JsonUrl = {};
  const hashes = url.slice(url.indexOf("?") + 1).split("&");
  for (let i = 0; i < hashes.length; i++) {
    hash = hashes[i].split("=");
    JsonUrl[hash[0]] = hash[1];
  }
  const code = JsonUrl.code;
  const details = {
    "redirect_uri": redirectURL,
    "client_id": "poplab-front",
    "grant_type": "authorization_code",
    "code": code,
    "scope": "openid",
  };
  let detailsBody = [];
  for (const property in details) {
    const encodedKey = encodeURIComponent(property);
    const encodedValue = encodeURIComponent(details[property]);
    detailsBody.push(`${encodedKey}=${encodedValue}`);
  }
  detailsBody = detailsBody.join("&");
  let user = {};
  user = await fetch(`${urlAuth}/token`, {
    method: "POST",
    headers: {
      "Content-Type": "application/x-www-form-urlencoded",
    },
    body: detailsBody,
  })
    .then((response) => response.json())
    .then((response) => {
      const userDetail = JSON.parse(atob(response.access_token.split(".")[1]));
      // Il faut decoder le pr√©nom de l'utilisateur pour g√©rer les accents
      const userFirstname = userDetail.given_name;
      const fixedFirstname = decodeURIComponent(escape(userFirstname));
      // user.first_name = fixedFirstname;
      localStorage.refresh_token = response.refresh_token;
      localStorage.access_token = response.access_token;
      localStorage.first_name = fixedFirstname;
      // user.refresh_token = response.refresh_token;
      // user.access_token = response.access_token;
      return user;
    });
  // await browser.storage.local.set({
  //   refresh_token: user.refresh_token,
  //   access_token: user.access_token,
  //   first_name: user.first_name,
  // });
  updateMenus(true);
  return user;
}

/**
     * Construction de la requ√™te fetch de cr√©ation de l'√©l√©ment scrapp√© avec ses attributs (titre, description, cr√©dits,...)
     * @param {objet array} variables
     * @returns {objet url} - Une url qui sera "slic√©e"" pour en extraire les donn√©es via un split
     */
async function requeteApi(variables) {
  const json = {};
  const index = [];
  if (variables) {
    for (const variable in variables.data) {
      index.push(variables.data[variable]);
    }
    // Tableau des valeurs de cl√©s
    const keys = Object.getOwnPropertyNames(variables.data);
    for (let i = 0; i < keys.length; i++) {
      json[keys[i]] = index[i];
    }
  }
  const headers = {
    "content-type": "application/json",
    "Accept": "application/json",
  };
  const storage = await browser.storage.local.get("access_token");
  if (storage.access_token) {
    headers.authorization = `Bearer ${storage.access_token}`;
  }
  const res = await fetch(`${urlApi}/elements/`, {
    method: "POST",
    headers,
    body: JSON.stringify(json),
  });

  return await res.json();
}

/**
     * Construction de la requ√™te fetch de cr√©ation de l'√©l√©ment de type objet scrapp√© avec ses attributs (titre, description, cr√©dits,...)
     * @param {objet array} variables
     * @returns {objet url} - Une url qui sera "slic√©e"" pour en extraire les donn√©es via un split
     */
async function uploadFichier(type, fichier, variables) {
  const response = await requeteApi(variables);
  const elementUUID = response.data.element.id;
  const body = new FormData();
  const index = [];
  variables[type] = null;
  // On r√©cup√®re la valeur de chaque propri√©t√© de "variables"
  for (const variable in variables.data) {
    index.push(variables.data[variable]);
  }
  // Tableau des valeurs de cl√©s
  const keys = Object.getOwnPropertyNames(variables.data);
  const jsonStr = {};
  for (let i = 0; i < keys.length; i++) {
    jsonStr[keys[i]] = index[i];
    body.append([keys[i]], index[i]);
  }
  body.append("file", fichier);
  body.append("uuid", elementUUID);
  body.append("objectType", "element");
  body.append("mediaType", "image");

  const storage = await browser.storage.local.get("access_token");
  const headers = {
    // Ne pas d√©finir le content-type pour qu'il soit d√©t√©ct√© automatiquement.
    // Si d√©fini ici, la requ√™te ne passe pas pour un souci de boundary dans le content-type
    // "Content-Type": "multipart/form-data"
  };
  if (storage.access_token) {
    headers.authorization = `Bearer ${storage.access_token}`;
  }

  const res = await fetch(`${urlApi}/v1/medias/upload`, {
    method: "POST",
    headers,
    body,
  });
  return await res.json();
}

// V√©rifie que le refresh_token est encore valide
// Cette fonction fait un appel HTTP, il faut donc √©viter de l'appeler autant que possible.
async function estConnecte() {
  const storage = localStorage.refresh_token;
  if (!storage) {
    return false;
  }
  return true;
}

async function seDeconnecter() {
  localStorage.clear();
  const logoutURL = `${urlAuth}/logout`;
  try {
    const url = await browser.identity.launchWebAuthFlow({
      interactive: false,
      url: logoutURL,
    });
  } catch (err) {
    await updateMenus(false);
  }
}

export {
  login, requeteApi, uploadFichier, estConnecte, seDeconnecter, updateMenus,
};
