browser.runtime.sendMessage({ type: "status" }).then((res) => {
  if (res === "connect√©") {
    window.close();
  }
  if (res === "d√©connect√©") {
    const switchVsibility = document.getElementById("error_cant_use_webclipper");
    if (switchVsibility) switchVsibility.style.visibility = "hidden";
  }
  if (res === "interdit") {
    // document.getElementById("error-cant-use-webclipper").style.display = "visible";
    const switchVsibility = document.getElementById("error_cant_use_webclipper");
    if (switchVsibility) switchVsibility.style.visibility = "visible";
  }
});

