import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    selectedTags: [],
  },
  getters: {
    selectedTags(state) {
      return state.selectedTags;
    },
  },
  mutations: {
    setSelectedTags(state, tag) {
      state.selectedTags.push(tag);
    },
  },
  actions: {
  },
  modules: {
  },
});
