    <div
      id="param"
    >
      <h3>Tag automatique</h3>
      <div
        id="tag"
      >
        <IconHelp />
        Les tags que vous s√©lectionnez seront ajout√©s √† toutes vos captures
      </div>
      <div
        class="tag-field"
      >
        <div
          id="tag_input"
        >
          <div
            id="search"
          >
            <p>
              <img
                src="@/assets/images/zoom_in_black_24dp.svg"
