              placeholder="Saisissez un mot cl√©..."
              @keydown.enter="addUserTag(userTags)"
              @keydown.esc="onKeyPressed(event)"
            >
          </div>
        </div>
      </div>
      <div
        class="selection"
      >
        <h3>
          S√©lection
        </h3>
        <ul
          id="tag_selected_list"
        >
          <li
            v-for="tag in selectedTags"
            :key="tag"
            class="tag-selected"
          >
            <label>
              <span>
                {{ tag }}
                <button
                  class="remove-tag"
                  type="button"
                  @click="removeTag(tag)"
                >
                  x
                </button>
              </span>
            </label>
          </li>
        </ul>
      </div>
      <div
        class="doted-line"
      />
      <div
        class="capture-defaut"
      >
        <h3>
          Capture par d√©faut
        </h3>
        <form
          class="capture-choice"
        >
          <span>
            <input
              id="radio_signet"
              class="radio-input"
              type="radio"
