<script>
import { mapGetters } from "vuex";
import IconHelp from "./IconHelp.vue";

export default {
  name: "Param",
  components: {
    IconHelp,
  },
  data() {
    return {
      userTags: "",
      favoriteTags: [],
      taille: 0,
      favoriteTagsList: [{ tag: "" }],
      dataFields: [],
    };
  },
  computed: {
    ...mapGetters(["selectedTags"]),
  },
  mounted() { // On r√©cup√®re les tags favoris de l'utilisateur
    this.favoriteTags = JSON.parse(localStorage.getItem("tags"));
  },
  methods: {
    emitDeconnexion() {
      this.$emit("deconnexion", false);
    },
    addUserTag() { // On ajoute le tag √† la s√©lection et aux favoris
      this.$store.commit("setSelectedTags", this.userTags);
      localStorage.setItem("tags", JSON.stringify(this.$store.state.selectedTags));
      this.userTags = "";
    },
    onKeyPressed(evt) {
      // evt.preventDefault();
      const code = evt.key;
      if (code === "Escape") this.userTags = "";
      if (code === "Enter") {
        this.$store.commit("setSelectedTags", this.userTags);
        localStorage.setItem("tags", JSON.stringify(this.$store.state.selectedTags));
        this.userTags = "";
      }
    },
    clearTag() {
      this.userTags = "";
    },
    removeTag(index) { // Suppression d'un tag de la s√©lection
      const pos = this.selectedTags.indexOf(index);
      if (pos > -1) {
        this.selectedTags.splice(pos, 1);
      }
    },
  },
};
</script>
